import { HttpClient } from '@angular/common/http';
import { NgModule, APP_INITIALIZER  } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MnDockerService } from './mn-docker.service';

export function init_app(dockerService: MnDockerService) {
  this.dockerService = dockerService;
  function ret() {
    dockerService.loadConfigs();
  }
  return ret;
}

@NgModule({
  imports: [
    CommonModule
  ],
  declarations: [],
  providers: [
    MnDockerService,
    { provide: APP_INITIALIZER, useFactory: init_app, deps: [MnDockerService], multi: true },
  ]
})
export class MnDockerModule { }
