import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import { Injectable, Optional, SkipSelf } from '@angular/core';

declare var mn_docker_data;

@Injectable()
export class MnDockerService {

  private fileName = 'assets/env.json';
  private conf: any = {};

  constructor() {
    console.log('DockerService creating');
    this.conf = mn_docker_data;
  }

  public loadConfigs() {
    console.log('DockerService created', this.conf);
  }

  public getEnv(variableName: string, def: any = ''): string | any {
    let ret = '';
    if (Object.keys(this.conf).indexOf(variableName) >= 0) {
      ret = this.conf[variableName];
    } else {
      ret = def;
    }
    return ret;
  }

  public getEnvAsArray(variableName: string, def: any = ''): any[] {
    const env = this.getEnv(variableName, def);
    return JSON.parse(env.replace(/'/g, '"'));
  }

  public getEnvironment() {
    return this.conf;
  }
}
